@import url(https://fonts.googleapis.com/css?family=Cinzel|Fondamento|Fredericka+the+Great|Simonetta&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Charm&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
	margin: 0;padding:0; width:100%;height: 100%;position: relative;
}


body{
	position:static;
	width: 100%;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	color: rgb(212, 203, 203);
	background-image: url(/Portfolio/static/media/texture.3bcafcb1.jpg);
  	background-size: cover;
	background-blend-mode: lighten;
	
}

header{
	background-color: rgb(0, 0, 0);
	background-image: url(/Portfolio/static/media/football.af2e751b.png);
}

*:link,
*:visited,
*:hover,
*:active {
  text-decoration: none; 

}


.links:link{color: white;}
.links:visited{color: white;}
.menu-item{padding: 5px; height: 25px; color: black; border-radius: 25%; margin: 5px}

.App {

  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

